




























body {
  font-family: Chinese Quote,-apple-system,BlinkMacSystemFont,Segoe UI,PingFang SC,Hiragino Sans GB,Microsoft YaHei,Helvetica Neue,Helvetica,Arial,sans-serif,Apple Color Emoji,Segoe UI Emoji,Segoe UI Symbol;
  font-size: 15px;
  color: #303133;
}
body a {
    color: #1890ff;
    text-decoration: none;
    font-size: 14px;
}
.el-divider--horizontal {
  margin: 0;
}
.home .sp {
  color: #1890ff;
}
.body_contain {
  padding-top: 86px;
  width: 1055px;
  margin: 0 auto;
}
.body_contain .body_item {
    font-size: 14px;
    -webkit-box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
            box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
    padding: 20px;
    margin-bottom: 20px;
}
.body_contain .body_item .content.four {
      line-height: 100px;
      text-align: center;
      height: 100px;
}
.body_contain .body_item .pl {
      padding: 20px 50px;
}
.body_contain .body_item .pl .pc {
        float: left;
}
.body_contain .body_item .pl .pc img {
          height: 50px;
}
.body_contain .body_item .pl .inpu {
        float: left;
        margin-left: 20px;
}
.body_contain .body_item .pl .inpu .inpu_b {
          margin-top: 30px;
}
.body_contain .body_item .pl .inpu .inpu_b button {
            background-color: #40a9ff;
            color: #ffffff;
            border: #40a9ff;
            width: 64px;
            height: 32px;
            border-radius: 4px;
            font-size: 13px;
}
.body_contain .body_item .content {
      padding: 20px 0;
      padding-right: 20px;
      border-bottom: 1px solid #ebeef5;
}
.body_contain .body_item .content .content_li {
        padding: 10px;
}
.body_contain .body_item .content .content_li .ac_img {
          float: left;
}
.body_contain .body_item .content .content_li .ac_img img {
            height: 50px;
}
.body_contain .body_item .content .content_li .test {
          float: left;
          padding-left: 10px;
}
.body_contain .body_item .content .content_li .test a {
            font-weight: 1000;
}
.body_contain .body_item .content .content_li .test .test_title {
            padding-bottom: 10px;
}
.body_contain .body_item .title {
      padding-top: 0;
      border-bottom: 1px solid #ebeef5;
}
.body_contain .body_item .title span.right {
        float: right;
        color: #409eff;
        cursor: pointer;
}
.body_contain .body_item .body_nav {
      margin-top: 15px;
}
.body_contain .body_item .body_nav .nav_left {
        float: left;
        text-align: center;
}
.body_contain .body_item .body_nav .nav_left p {
          margin: 6px 0;
}
.body_contain .body_item .body_nav .nav_left img {
          width: 272px;
          height: 400px;
          border-radius: 10px;
          -webkit-box-shadow: 0 0 10px #999;
                  box-shadow: 0 0 10px #999;
}
.body_contain .body_item .body_nav .nav_middle {
        padding: 0 20px;
        float: left;
        width: 460px;
}
.body_contain .body_item .body_nav .nav_middle .dao {
          float: left;
}
.body_contain .body_item .body_nav .nav_middle .daoyan {
          display: inline-block;
}
.body_contain .body_item .body_nav .nav_middle .daoyan img {
            border-radius: 10px;
}
.body_contain .body_item .body_nav .nav_middle h1 {
          font: 36px Arial,sans-serif;
          font-weight: 400;
          line-height: 110%;
          margin: 0;
          color: #303133;
          padding-bottom: 3px;
}
.body_contain .body_item .body_nav .nav_middle p.title {
          font-style: italic;
          margin: 8px 0;
}
.body_contain .body_item .body_nav .nav_middle img {
          height: 133px;
          vertical-align: top;
}
.body_contain .body_item .body_nav .nav_middle span.top {
          margin-right: 10px;
}
.body_contain .body_item .body_nav .nav_middle .middle_type {
          margin-bottom: 10px;
}
.body_contain .body_item .body_nav .nav_right {
        float: left;
        width: 240px;
}
.body_contain .body_item .body_nav .nav_right .actor {
          float: left;
          height: 420px;
}
.body_contain .body_item .body_nav .nav_right .actor_list .item {
          margin-bottom: 10px;
}
.body_contain .body_item .body_nav .nav_right .actor_list .item img {
            height: 75px;
            border-radius: 10px;
            margin-right: 15px;
}
.body_contain .body_item .body_nav .nav_right .actor_list .item span {
            position: relative;
            top: -30px;
}
.head-contain {
  background: white;
  width: 100%;
  height: 52px;
  position: fixed;
  z-index: 4;
}
.head-contain .el-card__body {
    padding: 0;
}
.head-contain .header {
    position: relative;
    width: 1055px;
    margin: 0 auto;
}
.head-contain .header .header_info {
      margin: 0 20px;
      line-height: 52px;
}
.head-contain .header .search {
      display: inline-block;
      position: absolute;
      left: 30%;
      height: 52px;
      line-height: 52px;
}
.head-contain .header .search input {
        padding-left: 20px;
        border: 1px solid #dcdfe6;
        height: 28px;
        width: 300px;
        border-radius: 5px 0 0 5px;
}
.head-contain .header .search button {
        border-radius: 0 5px 5px 0;
}
.head-contain .header .person_info {
      position: absolute;
      right: 0;
      top: 52px;
      height: 100px;
      width: 100px;
      background: white;
}
.head-contain .header .person_info .keke {
        height: 30px;
        line-height: 30px;
        padding: 10px 20px;
        cursor: pointer;
}
.head-contain .header .people {
      float: right;
      height: 52px;
      width: 52px;
      border-radius: 50%;
      overflow: hidden;
}
.head-contain .header .people img {
        height: 100%;
        width: 100%;
}
